import Mock from "mockjs";
import banner from './banner'
import floor from './floor'
import address from './address'

Mock.mock("/api/banner/list", "get", () => {
    return {
        code: 200,
        message: "成功",
        data: banner
    }
})

Mock.mock("/api/floor/list", "get", () => {
    return {
        code: 200,
        message: "成功",
        data: floor
    }
})

Mock.mock("/api/address/list", "get", () => {
    return {
        code: 200,
        message: "成功",
        data: address
    }
})