// 引入表单校验包并使用
import VeeValidate from 'vee-validate';
// 引入表单校验中文包
import zh_CN from 'vee-validate/dist/locale/zh_CN'

export default function (Vue) {
    Vue.use(VeeValidate)
    VeeValidate.Validator.localize('zh_CN', {
        messages: {
            //将中文包展开
            ...zh_CN.messages,
            //重新覆盖正则的提醒
            regex: (n) => `${n}的值不符合格式`,
            is: (n) => `${n}的值必须和密码一致`
        },
        // 将这些格式的名字都转换成中文
        attributes: {
            phone: "手机号",
            verifyCode: "验证码",
            password: "密码",
            passwordAgain: "确认密码",
            agree: "协议"
        }
    })
    // 自定义校验规则
    VeeValidate.Validator.extend('agree', {
        validate: (value) => value,
        getMessage: (n) => `${n}必须同意`
    })
}