<template>
  <div class="type-nav">
    <div class="container">
      <div @mouseenter="isEnterShow = true" @mouseleave="isEnterShow = false">
        <h2 class="all">全部商品分类</h2>
        <transition name="category">
          <div class="sort" v-show="isCheckListShow">
            <div
              class="all-sort-list2"
              @mouseenter="isEnterCategory = true"
              @mouseleave="isleave"
              @click="toSearch"
            >
              <!-- 一级列表 -->
              <div
                class="item bo"
                v-for="(category1, index) in categoryList"
                :key="category1.categoryId"
                :class="ListisShow === index ? 'active' : ''"
                @mouseenter="mouseListShow(index)"
              >
                <h3>
                  <a
                    href="javascript:;"
                    :data-category1Id="category1.categoryId"
                    :data-categoryName="category1.categoryName"
                  >
                    {{ category1.categoryName }}
                  </a>
                </h3>
                <div class="item-list clearfix">
                  <div class="subitem">
                    <!-- 二级列表 -->
                    <dl
                      class="fore"
                      v-for="category2 in category1.categoryChild"
                      :key="category2.categoryId"
                    >
                      <dt>
                        <a
                          href="javascript:;"
                          :data-category2Id="category2.categoryId"
                          :data-categoryName="category2.categoryName"
                        >
                          {{ category2.categoryName }}
                        </a>
                      </dt>
                      <!-- 三级列表 -->
                      <dd>
                        <em
                          v-for="category3 in category2.categoryChild"
                          :key="category3.categoryChild"
                        >
                          <a
                            href="javascript:;"
                            :data-category3Id="category3.categoryId"
                            :data-categoryName="category3.categoryName"
                          >
                            {{ category3.categoryName }}
                          </a>
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav">
        <a href="###">服装城</a>
        <a href="###">美妆馆</a>
        <a href="###">尚品汇超市</a>
        <a href="###">全球购</a>
        <a href="###">闪购</a>
        <a href="###">团购</a>
        <a href="###">有趣</a>
        <a href="###">秒杀</a>
      </nav>
    </div>
  </div>
</template>

<script>
import { throttle } from "lodash";
import { mapState } from "vuex";
export default {
  name: "TypeNav",
  data() {
    return {
      ListisShow: -1,
      isEnterCategory: false,
      // 在其他页面 移入移出是否显示
      isEnterShow: false,
    };
  },
  mounted() {
    this.mouseListShow = this.throttle(this.mouseListShowItem, 100, {
      leading: true, //指定调用在节流开始前
      trailing: true, //指定调用在节流结束后
    });
  },
  methods: {
    // 执行的代码
    mouseListShowItem(index) {
      if (!this.isEnterCategory) return;
      this.ListisShow = index;
    },
    // 定义一个节流函数
    mouseListShow() {},
    // 将引入的throttle放在实例上
    throttle: throttle,
    //鼠标离开事件
    isleave() {
      this.ListisShow = -1;
      this.isEnterCategory = false;
    },
    //三级列表点击跳转到搜索页
    toSearch(e) {
      if (e.target.nodeName !== "A") return;

      const { category1id, category2id, category3id, categoryname } =
        e.target.dataset;

      this.$router.push({
        name: "search",
        query: {
          category1Id: category1id,
          category2Id: category2id,
          category3Id: category3id,
          categoryName: categoryname,
        },
        params: this.$route.params,
      });
    },
  },
  computed: {
    ...mapState("category", ["categoryList"]),
    isCheckListShow() {
      if (this.$route.name === "home") return true;

      return this.isEnterShow;
    },
  },
  watch: {
    $route() {
      this.isEnterShow = false;
    },
  },
};
</script>

<style scoped lang="less">
.category-enter,
.category-leave-to {
  opacity: 0;
}

.category-enter-active,
.category-leave-active {
  transition: all 0.5s linear;
}

.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }

          // &:hover {
          //   .item-list {
          //     display: block;
          //   }
          // }
          &.active {
            background: pink;
            .item-list {
              display: block;
            }
          }
        }
      }
    }
  }
}
</style>