import Vue from 'vue'
import VueRouter from 'vue-router'

import routes from '@/router/routes'
import store from '@/store'

Vue.use(VueRouter)

const router = new VueRouter({
    mode: 'history',
    routes,
    scrollBehavior() {
        return {
            x: 0,
            y: 0,
        }
    }
})

// 重写push
const oldPush = VueRouter.prototype.push
VueRouter.prototype.push = function (location, onComplete = () => { }, onAbort = () => { }) {
    oldPush.call(this, location, onComplete, onAbort)
}


// 路由白名单
const whiteList = ['addcartsuccess', 'detail', 'home', 'login', 'register', 'search']

router.beforeEach(async (to, from, next) => {
    const token = store.state.users.token

    // 判断有没有token
    if (token) {
        // 判断用户是不是要前往login页
        if (to.name === 'login') {
            next('/home')
        } else {
            const userInfo = !!store.state.users.userInfo.nickName
            // 判断用户信息是否存在，如果存在直接放行，如果不存在去请求用户数据,然后再放行
            if (userInfo) {
                next()
            } else {
                try {
                    await store.dispatch("users/getUserInfo")
                    next()
                } catch (error) {
                    // 如果回去用户信息异常，说明token已经失效，需要清掉重新登录
                    store.dispatch('users/resetUser')
                    next('/login')
                    // alert(error.message)
                }
            }
        }
    } else {
        // 判断要去的路径是不是白名单
        if (whiteList.includes(to.name)) {
            next()
        } else {
            next('/login')
        }
    }
})

export default router