<template>
  <div class="pagination">
    <button :disabled="pageNo <= 1" @click="$emit('changePage', pageNo - 1)">
      上一页
    </button>
    <button @click="$emit('changePage', 1)" v-show="calcpage.start > 1">
      1
    </button>
    <button v-show="calcpage.start > 2">>···</button>

    <button
      @click="$emit('changePage', item)"
      :class="{ active: item === pageNo }"
      v-for="item in continuePageArr"
      :key="item"
    >
      {{ item }}
    </button>

    <button v-show="calcpage.end < totalPages - 1">···</button>
    <button v-show="calcpage.end < totalPages">{{ totalPages }}</button>
    <button
      @click="$emit('changePage', pageNo + 1)"
      :disabled="pageNo >= totalPages"
    >
      下一页
    </button>

    <!-- <button style="margin-left: 30px">共 60 条</button> -->
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: ["totalPages", "continuePage", "pageNo"],
  computed: {
    calcpage() {
      const { totalPages, continuePage, pageNo } = this;
      let start = 0;
      let end = 0;

      if (continuePage > totalPages) {
        start = 1;
        end = totalPages;
      } else {
        start = pageNo - Math.floor(continuePage / 2);
        end = pageNo + Math.floor(continuePage / 2);

        if (start < 1) {
          start = 1;
          end = continuePage;
        }

        if (end > totalPages) {
          end = totalPages;
          start = totalPages - continuePage + 1;
        }
        //   console.log(start, end);
      }
      return { start, end };
    },

    continuePageArr() {
      const { start, end } = this.calcpage;
      const arr = [];
      for (let i = 1; i <= end; i++) {
        if (i >= start) {
          arr.push(i);
        }
      }
      return arr;
    },
  },
};
</script>

<style lang="less" scoped>
.pagination {
  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: #409eff;
      color: #fff;
    }
  }
}
</style>