import getUserTempId from "@/utils/userTempId"
import { SET_TOLOGIN_STATE, SET_USERINFO_STATE, DELETE_TOKEN_STATE } from '../constant-type'
import { reqToLogin, reqUserInfo, reqLogout } from "@/api";
import { getToken, setToken, removeToken } from "@/utils/token";

const state = {
    userTempId: getUserTempId(),
    token: getToken() || '',
    userInfo: ''
}

const mutations = {
    [SET_TOLOGIN_STATE](state, payLoad) {
        state.token = payLoad.token
        // 永久化存储token
        setToken(payLoad.token)
    },
    [SET_USERINFO_STATE](state, payload) {
        state.userInfo = payload.userInfo
    },
    // 退出登录删除token
    [DELETE_TOKEN_STATE](state) {
        state.token = ""//清除vuex中的token
        removeToken()//清除本地存储中的token
        state.userInfo = {}//清除用户信息
    }
}

const actions = {
    // 请求token并放在state中
    async reqToken({ commit }, payLoad) {
        try {
            let res = await reqToLogin({
                phone: payLoad.userName,
                password: payLoad.password,
            });
            // console.log(res);
            commit(SET_TOLOGIN_STATE, { token: res.token })
        } catch (error) {
            alert(error.message);
            return Promise.reject()
        }
    },
    // 请求用户信息数据并放在state中
    async getUserInfo({ commit }) {
        try {
            let res = await reqUserInfo()
            commit(SET_USERINFO_STATE, { userInfo: res })
        } catch (error) {
            alert(error.message)
            return Promise.reject()//因为这个trycatch处理了异常，所以在路由守卫中无法得到异常的值，进入不了路由守卫中的catch所以只能在这里抛出失败的promise
        }
    },
    // 退出登录
    async getLogout({ commit }) {
        try {
            await reqLogout()
            commit(DELETE_TOKEN_STATE)
        } catch (error) {
            alert(error.message)
        }
    },
    // 一旦token失效清理user数据
    resetUser({ commit }) {
        commit(DELETE_TOKEN_STATE)
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}