const Home = () => import('@/pages/Home')
const Login = () => import('@/pages/Login')
const Register = () => import('@/pages/Register')
const Search = () => import('@/pages/Search')
const Detail = () => import('@/pages/Detail')
const AddCartSuccess = () => import('@/pages/AddCartSuccess')
const ShopCart = () => import('@/pages/ShopCart')
const Trade = () => import('@/pages/Trade')
const Pay = () => import('@/pages/Pay')
const PaySuccess = () => import('@/pages/PaySuccess')
const Center = () => import('@/pages/Center')

export default [
    {
        path: '/',
        redirect: '/home' //路由重定向
    },
    {
        path: '/home',
        name: 'home',
        component: Home
    },
    {
        path: '/login',
        name: 'login',
        component: Login,
        meta: {
            isShow: true
        }
    },
    {
        path: '/register',
        name: 'register',
        component: Register,
        meta: {
            isShow: true
        }
    },
    {
        path: '/search/:keyword?',
        name: 'search',
        component: Search
    },
    {
        path: '/detail/:skuId',
        name: 'detail',
        component: Detail
    },
    {
        path: '/addcartsuccess',
        name: 'addcartsuccess',
        component: AddCartSuccess,
        //路由独享守卫
        beforeEnter: (to, from, next) => {
            if (from.name === 'detail') {
                next()
            } else {
                next('detail')
            }
        }
    },
    {
        path: '/shopcart',
        name: 'shopcart',
        component: ShopCart
    },
    {
        path: '/trade',
        name: 'trade',
        component: Trade,
        //路由独享守卫
        beforeEnter: (to, from, next) => {
            if (from.name === 'shopcart') {
                next()
            } else {
                next('shopcart')
            }
        }
    },
    {
        path: '/pay',
        name: 'pay',
        component: Pay,
        //路由独享守卫
        beforeEnter: (to, from, next) => {
            if (from.name === 'trade') {
                next()
            } else {
                next('shopcart')
            }
        }
    },
    {
        path: '/paysuccess',
        name: 'paysuccess',
        component: PaySuccess,
        //路由独享守卫
        beforeEnter: (to, from, next) => {
            if (from.name === 'pay') {
                next()
            } else {
                next('shopcart')
            }
        }
    },
    {
        path: '/center',
        name: 'center',
        redirect: '/center/myorder',
        component: Center,
        children: [
            {
                path: 'myorder',
                name: 'myorder',
                component: () => import('@/pages/Center/component/MyOrder')
            },
            {
                path: 'teamorder',
                name: 'teamorder',
                component: () => import('@/pages/Center/component/TeamOrder')
            },
        ]
    },
]